@import "normalize";
@import "base/typography.scss";
@import "utils/variables.scss";
@import "utils/responsive.scss";
@import 'utils/nav.scss';

html {
    scroll-behavior: smooth;
}

body {
    max-width: 1920px;
    margin: 0 auto;  
    font-family: 'PT Serif', sans-serif;
    overflow-x: hidden;   
}

.wrap {
    width: 1140px;
    margin: 0 auto;
}

@include r(1420) {
    .wrap {
        width: 960px;
    }
}

@include r(1024) {
    .wrap {
        width: 720px;        
    }
}

@include r(768) {
    .wrap {
        width: 390px;
    }
}

@include r(480) {
    .wrap {
        width: 300px;
    }
}

// Header

.header {
    position: relative;       
    height: 100%;
    background-image: url('../img/headerbg768.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-color: #101010; 
}

@media (min-width: 1420px) {
    .header {        
        background-image: url('../img/headerbg.png');    
    }
}


@media (min-width: 1024px) and (max-width: 1419.98px) {
    .header {        
        background-image: url('../img/headerbg1420.png');    
    }
}

@media (min-width: 768.02px) and (max-width: 1023.98px) {
    .header {        
        background-image: url('../img/headerbg1024.png');    
    }
}

@include r(1420) {
    .header {
        background-position: 40%;
    }
}

@include r(768) {
    .header {
        background-position: 25%;    
    }
}

// .header__bg {
//     position: absolute;
//     z-index: -1;
//     background-color: #101010;
//     width: 100%;
//     height: 100%;
// }

.header__top {
    display: flex;
    justify-content: space-between;

    padding: 40px;      
}

@include r(480) {
    .header__top {
        padding: 20px;
    }    
}

.header__logo {
    position: relative;    
}

.header__logo-pic {
    position: absolute;
    top: 20px; 
}

.header__logo-name {
    position: absolute;
    top: 10px;
    left: 27px;    

    font-family: "CafFranoise", sans-serif;
    font-size: 45px;
    color: $bs-yellow;
    text-transform: uppercase;
    white-space: nowrap;
}

@include r(1024) {
    .header__logo-name {
        font-size: 25px;
        top: 15px;
        left: 18px;
    }
}

@include r(480) {
    .header__logo-pic {        
        top: 0;
        left: 0; 
    }

    .header__logo-name {        
        top: -5px;
        left: 20px;
    }
}

.header__menu {
    margin-right: 5px;
}

@include r(1024) {
    .header__menu-icon {
        width: 30px;
        height: auto;
    }
}

.header__caption {
    text-align: center;

    font-style: italic;
    font-size: 40px;
    color: $bs-yellow;

    margin-top: 235px;
}

@include r(1420) {
    .header__caption {
        font-size: 35px;
        margin-top: 120px;
    }    
}

@include r(768) {
    .header__caption {
        font-size: 25px;
        margin-top: 120px;
    }  
}

.header__title {
    display: flex;
    justify-content: center;    

    font-family: "CafFranoise", sans-serif;
    font-size: 120px;
    color: $bs-yellow;
    text-transform: uppercase;
    
    margin-top: 20px;
}

@include r(1420) {
    .header__title {
       font-size: 100px;
    }
}

@include r(1024) {
    .header__title {
       font-size: 80px;
    }
}
.header__title-line {
    position: relative;

    margin-left: 40px; 
    &::after {
        content: '';
        width: 255px;
        height: 10px;
        background-color: $bs-yellow;        
        display: block;
        position: absolute;
        top: 65px;
        left: -20px;        
    }
}

@include r(1420) {
    .header__title-line {
        &::after {
            content: '';
            width: 210px;
            height: 10px;
            background-color: $bs-yellow;        
            display: block;
            position: absolute;
            top: 55px;
            left: -20px;        
        }
    }
}

@include r(1024) {
    .header__title-line {       
    &::after {
            content: '';
            width: 170px;
            height: 8px;
            background-color: $bs-yellow;        
            display: block;
            position: absolute;
            top: 45px;
            left: -20px;        
        }
    }
}

@include r(768) {
    .header__title-line {       
    &::after {
            content: '';
            width: 170px;
            height: 8px;
            background-color: $bs-yellow;        
            display: block;
            position: absolute;
            top: 45px;
            left: -20px;        
        }
    }
}
.header__button {
    display: flex;    
    justify-content: center;
    background-color: rgba($color: transparent, $alpha: 0.5);
    overflow: hidden;
    border: 3px solid $bs-yellow;
    border-radius: 10px;
    font-family: inherit;
    
    margin: -3px auto;
    margin-top: 35px; 
}

@include r(1420) {
    .header__button {        
        border: 2px solid $bs-yellow;        
        
        margin: -2px auto;
        margin-top: 25px; 
    }    
}

.header__button-text {
    font-size: 25px;
    text-transform: uppercase;
    color: $bs-yellow;
    white-space: nowrap;    

    border-right: 3px solid $bs-yellow;
    padding: 10px 20px;
}

@include r(1420) {
    .header__button-text {
        font-size: 20px;            
    
        border-right: 2px solid $bs-yellow;
        padding: 8px 15px;
    }
}

@include r(1024) {
    .header__button-text {
        font-size: 16px;            
    
        border-right: 2px solid $bs-yellow;
        padding: 6px 10px;
    }
}

.header__button-arrow {
    padding: 16px;    
}

@include r(1420) {
    .header__button-arrow {
    padding: 12px 14px;    
    }    
}

@include r(1024) {
    .header__button-arrow {
    padding: 8px 10px 3px;    
    }    
}

@include r(768) {
    .header__button-arrow {
    padding: 6px 8px;    
    }    
}

.header__pagination {
    display: flex;
    justify-content: center;
    text-transform: uppercase;
    color: $bs-yellow;
    font-size: 18px;
    
    margin: auto;
    margin-top: 200px;
}

@include  r(1420) {
    .header__pagination {       
        font-size: 16px;
        
        margin: auto;
        margin-top: 100px;
    }
}

.header__pagination-left {
    display: flex;
    align-items: center;
}

.header__pagination-text {
    padding: 0 5px;
}

.header__pagination-center {
    display: flex;
    color: #8d6c00;

    margin: 0 35px;
}

.header__pagination-active {
    color: $bs-yellow;
}

.header__pagination-right {
    display: flex;
    align-items: center;
    flex-direction: row-reverse
}

.header__arrow {
    text-align: center;

    margin-top: 30px;
    margin-bottom: 70px;
}

// Intro Section

.intro {    
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    position: relative;

    background-color: #101010;    
    margin-top: -1px;
}

@include r(1024px) {
    .intro {
        min-height: 500px;
    }
}

@include r(1420) {
    .intro__img-left {
        width: 545px;
        height: auto;
    }

    .intro__img-right {
        width: 350px;
        height: auto;
    }
}

@include r(1024) {
    .intro__img-left {
        display: none;
    }

    .intro__img-right {
        display: none;
    }
}

.intro__main {
    max-width: 1420px;
    position: absolute;
    left: 50%;    
    transform: translate(-50%);    
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;    
        
    background-image: url('../img/martin-arusalu480.png');
    background-repeat: no-repeat;          
}

@media (min-width: 1024px) {
    .intro__main {        
        background-image: url('../img/martin-arusalu.png');    
    }
}

@media (min-width: 768.02px) and (max-width: 1023.98px) {
    .intro__main {        
        background-image: url('../img/martin-arusalu1024.png');    
    }
}

@include r(1420) {
    .intro__main {
        max-width: 1100px;
        height: 100%;
        background-position: 30%;         
    }
}

@include r(1024) {
    .intro__main {               
        max-width: 768px;
        max-height: 100%;
        min-height: 450px;

        background-size: cover;
    }
}

@include r(768) {
    .intro__main {               
        max-width: 480px;
        max-height: 100%;
        min-height: 450px;

        background-size: cover;
    }
}

@include r(480) {
    .intro__main {               
        max-width: 320px;        
    }
}

.intro__main-wrap {
    width: 1140px;
    height: 100%;
    margin: 0 auto;   
    display: flex;
    justify-content: flex-end;
    align-items: center;

    padding: 0 140px;
}

@include r(1420) {
    .intro__main-wrap {
        width: 940px;
        
        padding: 0 30px;
    }
}

@include r(1024) {
    .intro__main-wrap {
        width: 750px;
        padding: 9px;
    }
}

@include r(768) {
    .intro__main-wrap {
        width: 480px;
        padding: 0;
        justify-content: center;       
    }
}

@include r(768) {
    .intro__main-wrap {
        width: 320px;              
    }
}
.intro__main-box {
    width: 560px;
    margin: 250px 0;   
    background-color: $bs-yellow;
     
}

@include r(1420) {
    .intro__main-box {
        width: 420px;
        margin: 100px 0;  
        background-color: $bs-yellow;
        
    }
}

@include r(1024) {
    .intro__main-box {
        width: 340px;
        margin: 100px 0;  
        background-color: $bs-yellow;
        
    }
}

.intro__main-inner {
    padding: 90px 40px;
}

@include r(1420) {
    .intro__main-inner {
        padding: 45px 30px;
    }

}

@include r(1024) {
    .intro__main-inner {
        padding: 35px 20px;
    }

}

.intro__main-title {      
    font-size: 60px;
    font-style: italic;
    
    margin: 0;  
}

@include r(1420) {
    .intro__main-title {      
        font-size: 50px;        
    }
}

.intro__main-subtitle {
    color: #010100;
    font-family: Lato, sans-serif;
    font-size: 25px;
    font-weight: 700;

    margin: 25px 0 0;
}

@include r(1420) {
    .intro__main-subtitle {        
        font-size: 20px;
        font-weight: 700;

        margin: 15px 0 0;
    }
}

.intro__main-text {
    color: #000000;
    font-family: "PT Serif";
    font-size: 19px;
    font-weight: 400;
    line-height: 35px;   

    margin: 30px 0 0;
}

@include r(1420) {
    .intro__main-text {        
        font-size: 16px;
        line-height: 26px;   

        margin: 25px 0 0;
    }
}

.intro__main-btn {    
    margin: 30px 0 0;
}

// Component Button

.button {
    display: flex;
    border: 3px solid #000000;
    border-radius: 10px;

    background-color: rgba($color: $bs-yellow, $alpha: 0.5);
}

.button__text {
    font-size: 25px;
    text-transform: uppercase;
    border-right: 3px solid;
    margin: -1px 0;

    padding: 10px 20px;
}

.button__arrow {
    fill: #000000;

    padding: 15px 10px 10px 15px;
}

@include r(1420) {
    .button {        
        border: 2px solid #000000;
    }

    .button__text {
        font-size: 20px;        
        border-right: 2px solid;
        margin: -1px 0;

        padding: 10px 20px;
    }

    .button__arrow {
        fill: #000000;

        padding: 12px 5px 5px 12px;
    }
}

@include r(480) {
    .button {        
        border: 1px solid #000000;
    }

    .button__text {
        font-size: 16px;        
        border-right: 1px solid;
        margin: -1px 0;

        padding: 5px 10px;
    }

    .button__arrow {
        fill: #000000;

        padding: 4px 5px 4px 8px;
    }
}

//End Component

// Event Section

.event {
    position: relative;
    height: 1200px;         
}

@include r(768) {
    .event {
        height: auto;
    }    
}

.event__bg {
    position: absolute;
    width: 100%;
    height: 100%;    
    display: flex;
    justify-content: space-between;
    z-index: -1; 
    background-color: #202020;
}

@include r(480) {
    .event__bg {            
        display: flex;
        flex-direction: column;
        justify-content: flex-start;        
    }
}

.event__img-right {
    align-self: flex-end;    
}

@include r(1024) {
    .event__img-right {
        align-self: center;
    }
}

.event__wrap {
    color: white;
    display: flex;
    justify-content: space-between;

    padding: 115px 0 0;
}

@include r(1420) {
    .event__wrap {
        padding: 90px 0 0;
    }
}

@include  r(1024px) {
    .event__wrap {
        display: block;
    }
}

// Component Caption 

.caption {
    font-size: 36px;
    font-weight: bold;
    
    &:after {
        content: "";
        display: block;
        width: 30px;
        height: 3px;
        background-color: white;
        margin-top: 15px;        
    }
}

@include r(480) {
    .caption {
        font-size: 26px;        
    }
}

//End Component

.event__content { 
   width: 740px;
}

@include r(1420) {
    .event__content { 
        width: 650px;
    }
}

@include r(1024) {
    .event__content {
        margin-top: 10px;
    }
}

@include r(768) {
    .event__content {
        width: 390px;
    }
}

@include r(480) {
    .event__content {
        width: 300px;
    }
}

.event__text {
    margin: 0 0 40px;
    line-height: 26px;
}

.event__schedule {
    font-weight: bold;
    color: #f7ce2d;

    padding-top: 15px;    
}

.event__date {
    font-size: 26px;

    margin-right: 25px;
}

@include r(480) {
    .event__date {
        display: block;
        margin-bottom: 10px;
    }
}

.event__time {
    font-size: 19px;
}

.event__list {
    font-size: 19px;

    margin: 30px 0 0;
}

@include r(480) {
    .event__list {
        font-size: 16px;
    }    
}

.event__item {   
    line-height: 30px;

    margin: 0 0 25px;

    &:last-child {
        margin-bottom: 0;
    }
}

@include r(480) {
    .event__item {   
    line-height: 26px;

    margin: 0 0 15px;

    &:last-child {
        margin-bottom: 0;
    }
}
}

.event__item-inner {
    margin-left: 10px;
}

.event__form-wrap {
    width:  760px;    
    position: absolute;
    margin: 100px 0 0;
    left: 50%;
    transform: translate(-50%);
    background-color: $bs-yellow;
}

@include r(1420) {
    .event__form-wrap {
        width: 700px;
        margin: 70px 0 0;        
    }
}

@include r(768) {
    .event__form-wrap {
        position: static;
        transform: none;
        margin: 20px auto;
        width: 390px;
    }
}

@include r(480) {
    .event__form-wrap {        
        width: 320px;
    }
}

.event__form {
    padding: 70px 45px;
}

@include r(1420) {
    .event__form {
        padding: 40px 25px;        
    }
}

@include r(480) {
    .event__form {
        padding: 20px 10px;        
    }
}
.event__form-title {
    text-align: center;
    color: #010100;
    font-family: Lato;
    font-size: 36px;
    font-weight: 700;

    margin: 0 0 30px;
}

@include r(768) {
    .event__form-title {
        font-size: 30px;
    }
}

@include r(480) {
    .event__form-title {
        font-size: 26px;
    }
}

.event__form-inner {
    display: flex;
    flex-wrap: wrap;

    justify-content: space-between;
}

.event__form-cell {
    width: 320px;
    display: flex;
    flex-direction: column;
    
    margin-bottom: 30px;
}

@include r(1420) {
    .event__form-cell {
        width: 300px;        
    }
}

@include r(480) {
    .event__form-cell {
        width: 320px;
        margin-bottom: 5px;        
    }
}

@include r(480) {
    .event__form-cell {
        width: 280px;
        margin-bottom: 10px;        
    }
}
.event__cell-name {
    font-size: 19px;
    font-weight: 700;
    color: #010100;

    margin-bottom: 15px;
}

@include r(480) {
    .event__cell-name {
        font-size: 16px;
        font-weight: 700;
        color: #010100;

        margin-bottom: 15px;
    }
}

.event__cell-input {
    height: 45px;
    border: 3px solid #000000;
    border-radius: 10px;
    background-color: #dca800;
    padding-left: 20px;
    box-sizing: content-box;  

        &:placeholder-shown {
            padding: 0;
            padding-left: 20px;                       
        }

        &::placeholder {
            color: #010100;
            opacity: 0.5;
        }
}

.event__cell-select {
    height: 45px;   

    padding-left: 20px;
}

@include r(768) {
        .event__cell-input {
        height: 35px;
        border: 2px solid #000000;       
        padding-left: 0;
            &:placeholder-shown {
                padding: 0;
                padding-left: 10px;                       
            }
    }

    .event__cell-select {
        height: 35px;   

        padding-left: 5px;
    }
}

@include r(480) {
        .event__cell-input {
        height: 30px;
        border: 1px solid #000000;       
        padding-left: 0;
            &:placeholder-shown {
                padding: 0;
                padding-left: 10px;                       
            }
    }

    .event__cell-select {
        height: 30px;   

        padding-left: 10px;
    }
}

.event__form-btn {
    margin: 0 auto;
}

