    .contact__content {
        width: 300px;        
    }
}

.contact__text {
    margin: 0 0 26px;
}

.contact__text-color {
    color: $bs-yellow;
    font-size: 19px;
    line-height: 45px;
}

@include r(480) {
    .contact__text-color {
        color: $bs-yellow;
        font-size: 16px;
        line-height: 26px;
    }

}

// Footer 

.footer {
    background-color: #202020;
    color: #ffffff;
    font-size: 14px;

    padding: 23px 0;
}

.footer__wrap {
    display: flex;
    justify-content: space-between;    
}

@include r(480) {
    .footer__wrap {
        display: block;      
    }    
}

.footer__social {
    width: 150px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@include r(480) {
    .footer__social {
        margin-top: 10px;
    }
}